"use client";

import { useState, useEffect } from "react";
import "./send-email.css";
import AuthGuard from "@/components/AuthGuard";
import AdvanceEmailBuilder from "./advanceEmailEditor";
import { useRouter } from "next/navigation";
import EmailEditor from "react-email-editor";

interface SendEmailProps {
  emailId?: string;
}

export default function SendEmail({ emailId }: SendEmailProps) {
  const router = useRouter();

  const [to, setTo] = useState<string[]>([]);
  const [inputValue, setInputValue] = useState("");

  const [subject, setSubject] = useState("");
  const [body, setBody] = useState("");
  const [senderName, setSenderName] = useState("");

  const [loading, setLoading] = useState(false);
  const [error, setError] = useState<string | null>(null);
  const [success, setSuccess] = useState(false);

  const [draftId, setDraftId] = useState<string | null>(emailId || null);

  // Load logged-in user
  useEffect(() => {
    const user = JSON.parse(localStorage.getItem("user") || "{}");
    setSenderName(user?.email || "");
  }, []);

  // Load draft if emailId is present
  useEffect(() => {
    if (!emailId) return;

    const loadDraft = async () => {
      try {
        const res = await fetch(`/api/emails/${emailId}`, {
          headers: { Authorization: `Bearer ${localStorage.getItem("token")}` },
        });
        const data = await res.json();
        if (!res.ok) throw new Error(data.error);

        setTo(data.to || []);
        setSubject(data.subject || "");
        setBody(data.body || "");
        setSenderName(data.senderName || "");
        setDraftId(data._id);
      } catch (err) {
        console.error("Draft load error", err);
      }
    };

    loadDraft();
  }, [emailId]);

  // Email validation
  const isValidEmail = (email: string) =>
    /^[^\s@]+@[^\s@]+\.[^\s@]+$/.test(email);

  const addEmail = (email: string) => {
    email = email.trim();
    if (!isValidEmail(email)) {
      setError("Invalid email format");
      return;
    }
    if (!to.includes(email)) setTo([email, ...to]);
    setInputValue("");
    setError(null);
  };

  const handleKeyDown = (e: React.KeyboardEvent<HTMLInputElement>) => {
    if (e.key === "Enter" && inputValue.trim()) {
      e.preventDefault();
      addEmail(inputValue);
    }
  };

  const removeEmail = (email: string) => {
    setTo(to.filter((e) => e !== email));
  };

  const handlePaste = (e: React.ClipboardEvent<HTMLInputElement>) => {
    const paste = e.clipboardData.getData("text");
    const emails = paste.split(/[,;\s]+/);
    emails.forEach((email) => {
      if (isValidEmail(email) && !to.includes(email))
        setTo((prev) => [email, ...prev]);
    });
    e.preventDefault();
  };

  const isFormValid = () =>
    to.length > 0 && subject.trim() !== "" && body.trim() !== "";

  // Save draft
  const saveAsDraft = async () => {
    setLoading(true);
    setError(null);
    setSuccess(false);

    try {
      const res = await fetch("/api/draft", {
        method: "POST",
        headers: {
          "Content-Type": "application/json",
          Authorization: `Bearer ${localStorage.getItem("token")}`,
        },
        body: JSON.stringify({
          id: draftId,
          senderName,
          to,
          subject,
          body,
        }),
      });

      const data = await res.json();
      if (!res.ok) throw new Error(data.error);

      setDraftId(data._id); // save new draft ID if created
      router.push("/crm/draft-mail");
      setSuccess(true);
    } catch (err: any) {
      setError(err.message || "Failed to save draft");
    } finally {
      setLoading(false);
    }
  };

  // Send email
  const sendEmail = async () => {
    if (!isFormValid()) {
      setError("All fields are required");
      return;
    }

    setLoading(true);
    setError(null);
    setSuccess(false);

    try {
      const res = await fetch("/api/send", {
        method: "POST",
        headers: {
          "Content-Type": "application/json",
          Authorization: `Bearer ${localStorage.getItem("token")}`,
        },
        body: JSON.stringify({
          id: draftId,
          to,
          subject,
          body,
          senderName,
        }),
      });

      const data = await res.json();
      if (!res.ok) throw new Error(data.error);

      setSuccess(true);
      setTo([]);
      setSubject("");
      setBody("");
      setDraftId(null); // email sent, reset draftId
    } catch (err: any) {
      setError(err.message || "Failed to send email");
    } finally {
      setLoading(false);
    }
  };

  return (
    <AuthGuard>
      <div className="container">
        <div className="card">
          <h1>Send Email</h1>
          <p className="subtitle">Track opens in real-time</p>

          {error && <div className="error">{error}</div>}
          {success && (
            <div className="success">Email saved/sent successfully!</div>
          )}

          <label>To</label>
          <div className="email-wrapper">
            {to.map((email) => (
              <span key={email} className="chip">
                {email}
                <button onClick={() => removeEmail(email)}>Ã—</button>
              </span>
            ))}
            <input
              type="email"
              placeholder="Type email & press Enter"
              value={inputValue}
              onChange={(e) => setInputValue(e.target.value)}
              onKeyDown={handleKeyDown}
              onPaste={handlePaste}
            />
          </div>

          <label>Subject</label>
          <input
            value={subject}
            placeholder="Enter subject"
            onChange={(e) => setSubject(e.target.value)}
          />

          <label>Message</label>
          {/* <AdvanceEmailBuilder body={body} setBody={setBody} /> */}
          <EmailEditor body={body} setBody={setBody} />

          <button
            className="send-button ml-2"
            disabled={loading && !to.length}
            onClick={saveAsDraft}
          >
            {loading ? "Saving..." : "Save Email as Draft"}
          </button>

          <button
            className="send-button"
            disabled={!isFormValid()}
            onClick={sendEmail}
          >
            {loading && isFormValid() ? "Sending..." : "Send Email"}
          </button>
        </div>
      </div>
    </AuthGuard>
  );
}
